import React from 'react'
import fontawesome from '@fortawesome/fontawesome'
import { faPlus } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { useDispatch } from 'react-redux';
import { openModalUI } from '../../actions/ui';
fontawesome.library.add(faPlus);

export const AddFacturaButton = () => {
    const dispatch = useDispatch()

    const openModal = () => {
        dispatch(openModalUI())
    }
    return (
        <div className="container-add-button animate__animated animate__bounce animate__fadeInUp animate__fast">
            <button title="Agregar facturas" onClick={() => { openModal() }} className="btn btn-primary btn-factura-add">
                <FontAwesomeIcon icon={faPlus} size="sm" />
            </button>
        </div>
    )
}
