import React, { useState } from 'react'
import Modal from 'react-modal'
import fontawesome from '@fortawesome/fontawesome'

import { useDispatch, useSelector } from 'react-redux';
import { closeModalUI, finishLoadingUI, startLoadingUI } from '../../../actions/ui';

import { axiosUploadFactura } from '../../../helpers/axiosAPI';
import { mostrarMensaje } from '../../../helpers/popUp';
import { faUpload } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { SmallLoading } from '../../ui/SmallLoading';
import { obtenerFacturas } from '../../../actions/facturas';
fontawesome.library.add(faUpload);




Modal.setAppElement('#root');




export const ModalAdd = () => {

    const dispatch = useDispatch();
    const { modalOpen, isLoading } = useSelector(state => state.ui)
    const [files, setFiles] = useState([]);
    const [fileNames, setFileNames] = useState([])

    const handleChange = (event) => {
        setFiles([...event?.target?.files]);
        let arrNames = [];
        for (let i = 0; i < event?.target?.files?.length; i++) {
            arrNames?.push(
                {
                    name: event?.target?.files?.[i]?.name,
                    status: '',
                    size: `${event?.target?.files?.[i]?.size / 1000} kb`
                }
            );
        }
        setFileNames(arrNames)
    }

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            if (files.length === 0) {
                return mostrarMensaje('upload', 'Carga al menos una factura', 'warning', 'Aviso');
            }
            dispatch(startLoadingUI());
            const res = await uploadFile();
            if (!res?.data?.ok) {
                dispatch(finishLoadingUI());
                return mostrarMensaje('upload', res.data.msg || 'Error al cargar', 'error');
            }
            const { data: { content = [] } } = res;
            setFileNames(fileNames.map(fileName => ({
                ...fileName, status: content?.find(cont => {
                    return (fileName?.name === cont.content && cont.ok === false)
                }
                )?.msg || 'Factura cargada correctamente'
            })));

            dispatch(obtenerFacturas());
            dispatch(finishLoadingUI());
            if (content?.filter(con => con?.ok === true)?.length !== 0) {
                mostrarMensaje('upload', `'Subidos Correctamente' ${content?.filter(con => con?.ok === true)?.length} facturas`, 'success', 'Info')
            }
        } catch (err) {
            dispatch(finishLoadingUI());
            mostrarMensaje('upload', err, 'error');
        }

    }

    const uploadFile = () => {
        const formData = new FormData();
        files.forEach(file => {
            formData.append('factura', file);
        })
        return axiosUploadFactura('uploads/facturas', formData);
    }
    return (
        <Modal
            className="modal-add-factura ovf-y"
            overlayClassName="modal-fondo"

            isOpen={modalOpen}
            //  onAfterOpen={afterOpenModal}m/;[]
            onRequestClose={() => { }}
            closeTimeoutMS={200}
            shouldCloseOnEsc={true}
            contentLabel="EJEMPLO"
        >
            <div className="container">

                <div className="row d-flex justify-content-between">
                    <div className="col-md-6">
                        <h2>
                            Carga tus Facturas
                        </h2>
                    </div>
                    <div className="col-md-4 col-sm-4 d-flex justify-content-end">

                        <input disabled={isLoading} type="file" id="file" className="inputfile disabled_upload" multiple name="file" onChange={handleChange} />
                        <label className="p-2" htmlFor="file"><FontAwesomeIcon className="mr-5" icon={faUpload} />
                            Cargar Facturas</label>
                    </div>
                </div>
                <hr />
            </div>
            <div className="container position-relative w-100 h-100 col-12 p-3 px-5">
                <div className="col-12 div-list-files ">
                    <div className='row col-12 d-flex flex-wrap justify-content-between pb-3'>
                        <div className='col-4 border-end border-bottom'>
                            <h2>
                                Nombre
                            </h2>
                        </div>
                        <div className='col-4 text-center border-bottom'>
                            <h2>
                                Tamaño
                            </h2>
                        </div>
                        <div className='col-4 text-center border-start border-bottom'>
                            <h2>
                                Estado
                            </h2>
                        </div>
                    </div>
                    {
                        fileNames.map((file, index) => {
                            return (<React.Fragment key={index + 'files'}>
                                <div className='row col-12 d-flex flex-wrap justify-content-between '>
                                    <div className='col-4 border-end'>
                                        {`${file?.name.slice(0, 20)}${file?.name?.length > 20 ? '...' : ''}`}
                                    </div>
                                    <div className='col-4 text-center'>
                                        {file?.size}
                                    </div>
                                    <div className='col-4 text-center border-start'>
                                        {
                                            file?.status === 'Factura cargada correctamente' ?
                                                <div className={'img-input-ok '}>&nbsp;</div> :
                                                <div className={file?.status ? 'img-input-error' : 'img-input-pendiente'}>&nbsp;</div>
                                        }
                                        {file?.status || 'Pendiente'}
                                    </div>
                                </div>
                            </React.Fragment>)
                        })
                    }

                </div>
                <div className="btn-group-facturas col-12 w-100 row d-flex  justify-content-end">
                    <button onClick={(e) => handleSubmit(e)} disabled={isLoading} className="btn btn-register col-6 d-flex flex-wrap justify-content-center">
                        Registrar Facturas
                        {(isLoading) &&
                            (<SmallLoading />)
                        }
                    </button>
                    <button
                        onClick={() => {
                            setFileNames([]);
                            setFiles([])
                            dispatch(closeModalUI())
                        }}
                        className="btn btn-danger col-2 mx-2"
                        disabled={isLoading}
                    >
                        Cerrar
                    </button>
                </div>
            </div>


        </Modal>
    )
}
