

import HomeSharp from '@material-ui/icons/HomeSharp'
import Widgets from '@material-ui/icons/Widgets'
import { lazy } from 'react';

const DatosGeneralesLazy=lazy(()=>import('../Pages/MainPages/DatosGenerales'));
const FacturasLazy=lazy(()=>import('../Pages/MainPages/Facturas'));
const ProductosLazy=lazy(()=>import('../Pages/MainPages/Productos'));


export const routesMenu = [
    {
        path: "info",
        label: "Datos Generales",
        component: DatosGeneralesLazy,
        icon: HomeSharp
    },
    {
        path: "facturas",
        label: "Facturas",
        component: FacturasLazy,
        icon: HomeSharp
    },
    {
        path: "producto",
        label: "Productos",
        component: ProductosLazy,
        icon: Widgets
    },

];