import { types } from "../types/types";

const initialState = {
    facturas: [],
    factura: null,
    modalFactura: false,
    datosGenerales: {},
    loading: false

}

export const facturasReducer = (state = initialState, action) => {
    switch (action.type) {
        case types.loadFacturas:
            return {
                ...state,
                facturas: [...action.payload]
            }
        case types.addFacturas:
            return {
                ...state,
                facturas: [...state.facturas, ...action.payload]
            }
        case types.showFactura:
            return {
                ...state,
                factura: action.payload,
                modalFactura: true,
            }
        case types.closeModalFactura:
            return {
                ...state,
                modalFactura: false,
            }
        case types.getDatosGenerales:
            return {
                ...state,
                datosGenerales: action.payload,
            }
        case types.startLoadingF:
            return {
                ...state,
                loading: true,
            }
        case types.stopLoadingF:
            return {
                ...state,
                loading: false,
            }
        default:
            return state;
    }
}