import React from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { cerrarSesion } from '../../actions/login';
import {  NavLink } from "react-router-dom"
import { routesMenu } from '../../utils/menuOption';
import ExitToAppRounded from '@material-ui/icons/ExitToAppRounded'


export const SideBar = () => {
    const dispatch = useDispatch();
    const { name } = useSelector(state => state.login)
    const handleCerrarSesion = () => {
        dispatch(cerrarSesion());
    }
    return (

        <div className="d-flex flex-column flex-shrink-0 text-white sidebar__factura">
            <a href="/" className="d-flex align-items-center mb-3 mb-md-0 me-md-auto text-white text-decoration-none">
                <span className="fs-4 animate__animated animate__bounce animate__fadeInLeft animate__fast p-3">{name}</span>
            </a>
            <hr />
            <ul className="nav nav-pills flex-column">
                <li className="nav-item m-0 p-0 w-100">
                    {
                        routesMenu.map((opt, index) => {
                            return (
                                <div key={'div' + index} className="opt-sidebar animate__animated animate__bounce animate__fadeInLeft animate__fast">
                                    <NavLink key={'link' + index} to={opt.path} className="nav-link text-white" aria-current="page">
                                        <opt.icon key={'opt-icon' + index} />
                                        {opt.label}
                                    </NavLink>

                                </div>
                            )
                        })
                    }

                </li>

                <li>
                    <p onClick={handleCerrarSesion} className="opt-sidebar nav-link text-danger pointer danger animate__animated animate__bounce animate__fadeInLeft animate__fast">
                        <ExitToAppRounded />  Cerrar Sesión
                    </p>
                </li>
            </ul>
        </div>
    )
}
