import { useNavigate } from "react-router-dom";
import Swal from "sweetalert2";
import { axiosContoken, axiosSintoken } from "../helpers/axiosAPI";
import { mostrarMensaje } from "../helpers/popUp";
import { types } from "../types/types"
import { finishLoadingUI, startLoadingUI } from "./ui";

export const startLogin = (correo, password) => {

    return async (dispacth) => {
        try {
            dispacth(startLoadingUI())
            const response = await axiosSintoken('auth/login', { correo, password }, 'POST');
            const { data } = response;
            console.log(data)
            if (!data?.ok) {
                dispacth(finishLoadingUI())
                return mostrarMensaje('login', data.errors?.[0]?.msg, 'error');
            }
            localStorage.setItem('token', data.token);
            dispacth(login({
                nombre: data.user.nombre,
                uid: data.user.uid
            }));
            dispacth(finishLoadingUI())
        } catch (error) {
            dispacth(finishLoadingUI())
            mostrarMensaje('login', error, 'error')
        }
    }


}

export const startRegister = (correo, password, nombre) => {
    return async (dispacth) => {
        try {
            const rol = 'ADMIN_ROLE';
            dispacth(startLoadingUI())
            const response = await axiosSintoken('usuarios', { correo, password, nombre, rol }, 'POST');
            const { data } = response;
            if (!data?.usuario) {
                dispacth(finishLoadingUI())
                return Swal.fire('Error', data?.errors?.[0]?.msg, 'error',)
            }
            dispacth(startLogin(correo, password));
            dispacth(finishLoadingUI())
        } catch (error) {
            dispacth(finishLoadingUI())
            mostrarMensaje('login', error, 'error')
        }
    }
}


export const login = (user) => {
    return {
        type: types.login,
        payload: user
    }
}

export const verifyToken = () => {
    return async (dispatch) => {
        try {
            if (!localStorage.getItem('token')) {
                return dispatch(finishCheck())

            }
            const { data } = await axiosContoken('auth');
            if (!data.ok) {
                dispatch(finishCheck())
                return mostrarMensaje('login', '', 'warning', data.msg);
            }
            localStorage.setItem('token', data.content.token);

            dispatch(checkLoging(data.content.usuario.nombre, data.content.usuario.uid));

        } catch (error) {
            mostrarMensaje('login', error, 'error');
            dispatch(finishCheck())
        }
    }
}

const finishCheck = () => {
    localStorage.clear();
    return {
        type: types.checkLogingFinish,
        payload: {
            checking: false,
        }
    }
}

export const checkLoging = (nombre, uid) => {
    return {
        type: types.login,
        payload: {
            uid,
            nombre
        }
    }
}

export const cerrarSesion = () => {
    localStorage.clear();
    return {
        type: types.logout
    }
}