import React from 'react'

export const LoadingScreen = ({ isLogin = true, smallLogin = false }) => {
    return (
        <>
            <div className={`${(!isLogin) ? 'loading-dashboard' : "loading"} row`}>
                {(isLogin || smallLogin) && <div className="align-self-center text-center justify-content ">
                    <div className="mt-20 d-flex justify-content-center">
                        <h1 className="fuente-facturas text-white mr-5">Cargando sesión
                        </h1>
                        <div className="ml-5 sk-chase">
                            <div className="sk-chase-dot"></div>
                            <div className="sk-chase-dot"></div>
                            <div className="sk-chase-dot"></div>
                            <div className="sk-chase-dot"></div>
                            <div className="sk-chase-dot"></div>
                            <div className="sk-chase-dot"></div>
                        </div>
                    </div>

                </div>}
            </div>

            {(!smallLogin) &&
                <div className='tetrominos'>
                    <div className='tetromino box2'></div>
                    <div className='tetromino box1'></div>
                    <div className='tetromino box3'></div>
                    <div className='tetromino box4'></div>
                </div>}
        </>
    )
}
