import { axiosContoken } from "../helpers/axiosAPI"
import { mostrarMensaje } from "../helpers/popUp";
import { types } from "../types/types";
import moment from "moment"
import { finishLoadingUI, startLoadingUI } from "./ui";

export const getDatosGenerales = () => {
    return async (dispatch) => {
        try {
            const dataGeneral = JSON.parse(localStorage.getItem('datosGenerales') ? localStorage.getItem('datosGenerales') : '');
            if (dataGeneral?.lastUpdate && moment(new Date()).isBefore(moment(dataGeneral?.lastUpdate).add(10, 'm'), 'm')) {
                dispatch(setDatosGenerales(dataGeneral?.data));
                return;
            }

        } catch (ex) { }
        dispatch(startLoading())
        try {
            const res = await axiosContoken('facturas/datosGenerales');
            if (!res?.data?.ok) {
                return mostrarMensaje('datosGenerales', res.data.msg || 'Error al obtener datos generales', 'error');
            }
            localStorage.setItem('datosGenerales', JSON.stringify({ data: res?.data?.content, lastUpdate: new Date() }))
            dispatch(setDatosGenerales(res?.data?.content));
        } catch (error) {
            mostrarMensaje('upload', error, 'error')
        }
        dispatch(stopLoading())
    }
}

export const startLoading = () => {
    return {
        type: types.startLoadingF
    }

}

export const stopLoading = () => {

    return {
        type: types.stopLoadingF
    }
}

const setDatosGenerales = (datosGenerales = {}) => {
    return {
        type: types.getDatosGenerales,
        payload: datosGenerales
    }
}

export const obtenerFacturas = () => {
    return async (dispatch) => {
        dispatch(startLoadingUI())
        try {

            const { data } = await axiosContoken('facturas?limit=100');
            if (!data.ok) {
                dispatch(finishLoadingUI())
                return mostrarMensaje('upload', 'Error al consultar las facturas', 'error')
            }
            dispatch(finishLoadingUI())
            return dispatch(setFacturas(data.content.facturas));
        } catch (error) {
            dispatch(finishLoadingUI())

            mostrarMensaje('upload', error, 'error')
        }

    }

}

const setFacturas = (facturas = []) => {
    return {
        type: types.loadFacturas,
        payload: [...facturas]
    }
}

export const agregarFacturas = (facturas = []) => {
    return {
        type: types.addFacturas,
        payload: [...facturas]
    }

}

export const openModalFactura = (factura) => {
    return {
        type: types.showFactura,
        payload: factura
    }

}

export const closeModalFactura = () => {
    return {
        type: types.closeModalFactura,
    }

}


