import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux';
import { useLocation, useNavigate } from 'react-router-dom';
import { FormLoginComponent } from './components/FormLoginComponent';
import { FormRegisterComponent } from './components/FormRegisterComponent';
import { LogoLoginComponent } from './components/LogoLoginComponent'

export const Login = () => {

  const [registrar, setRegistrar] = useState(false);
  const location = useLocation();

  const { uid } = useSelector(state => state.login)
  const navigate = useNavigate();
  const from = location.state?.from?.pathname || "/";
  useEffect(() => {
    if (uid) {
      navigate(from, { replace: true });
    }
  }, [uid])
  return (
    <>
      <div className="bgcontainer login__main">
        <div className="container p-200">
          <div className="row align-items-center">
            <div className="col-md-6">
              {(registrar) ?
                <FormRegisterComponent /> :
                <LogoLoginComponent />
              }
            </div>
            <div className="col-md-6 ">
              <div className="section-right-container animate__animated animate__bounce animate__fadeInUp animate__fast">
                {(registrar) ?
                  <LogoLoginComponent /> :
                  <FormLoginComponent />
                }
              </div>
            </div>

          </div>

        </div>
      </div>
      <div className="form-group boton-flotante-login">
        <div className="animate__animated animate__bounce animate__fadeInUp">
          <button
            onClick={() => { setRegistrar(!registrar) }}
            className="btn btn-primary form-control"
          >
            {(registrar) ? 'Login' : 'Registrarse'}
          </button>
        </div>
      </div>


    </>
  )
}
