import Modal from 'react-modal';
import React, { useState } from 'react'
import moment from 'moment';
import { colorsChart } from '../../../utils/colors';
// eslint-disable-next-line no-unused-vars
import Chart from 'chart.js/auto';
import { Line } from 'react-chartjs-2';

export const ModalProducto = ({ openModal, producto = {}, setOpenModal }) => {

    const [infoPreciosModal, setInfoPreciosModal] = useState(false)
    const [infoPrecios, setInfoPrecios] = useState(null)

    const filtrarFechas = (arr = []) => {
        const arrFiltrado = []
        arr.forEach(fec => {
            if (!arrFiltrado.includes(moment(fec?.fechaRegistro).format('MMM YY'))) {
                arrFiltrado.push(moment(fec?.fechaRegistro).format('MMM YY'))
            }

        })
        return arrFiltrado
    }

    const verFacturaProducto = (prec) => {
        setInfoPreciosModal(true);
        setInfoPrecios(prec)
    }

    return (
        <Modal
            className="modal-show-producto text-white"
            overlayClassName="modal-fondo"

            isOpen={openModal}
            //  onAfterOpen={afterOpenModal}
            onRequestClose={() => { }}
            closeTimeoutMS={200}
            shouldCloseOnEsc={true}
            contentLabel="EJEMPLO"

        >
            <h2>
                {producto?.producto?.descripcion}
            </h2>
            <div className="mt-5 position-relative w-100 h-100 ">

                <div className='col-12 d-flex flex-wrap position-relative overflow-auto'>
                    <div className='col-3 d-flex flex-wrap justify-content-between align-items-start border h-100 overflow-scroll  p-3 m-2'>
                        <div className='col-5 '><h5>
                            Fecha
                        </h5>
                            <hr />
                        </div>
                        <div className='col-5 '><h5>Precio</h5>  <hr />
                        </div>

                        {
                            producto?.precios?.map((prec, index) => {
                                return <div className='w-100 col-12 d-flex flex-wrap justify-content-center precio-prod ' onClick={() => verFacturaProducto(prec)} key={prec?.fechaRegistro + index}>
                                    <div className='col-5'>{moment(prec?.fechaRegistro).format('DD-MM-YYYY')}</div>
                                    <div className='col-5'>$ {prec?.precioUnitario?.toFixed(2)}</div>
                                </div>

                            })
                        }
                    </div>
                    <div className='col-8 m-2 p-3 border'>
                        <Line
                            datasetIdKey='id'
                            options={{
                                animations: {
                                    tension: {
                                        duration: 1000,
                                        easing: 'ease',
                                        from: 0.2,
                                        to: 0.4,
                                        loop: false
                                    }
                                },
                                scales: {
                                    grid: {
                                        color: 'white'
                                    },
                                    x: {
                                        grid: {
                                            color: '#494949e9',
                                            borderWidth: 1
                                        }
                                    },
                                    y: {
                                        grid: {
                                            color: '#494949e9',
                                            borderWidth: 1
                                        }
                                    }
                                },
                                layout: {
                                    padding: 40
                                },

                            }}
                            data={{
                                labels: [...filtrarFechas(producto?.precioss)],
                                datasets: [{
                                    label: `${producto?.producto?.descripcion}`,
                                    data: producto?.precios?.map(prec => ({ y: prec?.precioUnitario, x: moment(prec?.fechaRegistro).format('MMM YY') })),
                                    backgroundColor: [
                                        colorsChart[1]
                                    ],
                                    borderColor: [
                                        colorsChart[1]
                                    ],
                                    borderWidth: 1
                                },
                                ]
                            }}
                            plugins={[{
                                id: 'custom_canvas_background_color',
                                beforeDraw: (chart) => {
                                    const ctx = chart.canvas.getContext('2d');
                                    ctx.save();
                                    ctx.globalCompositeOperation = 'destination-over';
                                    ctx.fillStyle = '#212529';
                                    ctx.fillRect(0, 0, chart.width, chart.height);
                                    ctx.restore();
                                }
                            }]}
                        />
                    </div>
                </div>
                <button className='btn btn-danger btn-cerrar' onClick={() => setOpenModal(false)}>Cerrar</button>
            </div>
            <Modal
                className="modal-info-precio text-white p-5"
                overlayClassName="modal-fondo"
                isOpen={infoPreciosModal}
                closeTimeoutMS={200}
                shouldCloseOnEsc={true}
            >
                <div className='row col-12  d-flex flex-wrap justify-content-between'>
                    <h2>
                        {producto?.producto?.descripcion}
                    </h2>


                </div>
                <div className='row d-flex flex-wrap'>
                    <div className='col-6 my-3'>
                        Fecha
                    </div>
                    <div className='col-6 my-3'>
                        {moment(infoPrecios?.fechaRegistro).format('DD-MM-YY')}
                    </div>
                    <div className='col-6 my-3'>
                        Precio
                    </div>
                    <div className='col-6 my-3'>
                       $ {infoPrecios?.precioUnitario?.toFixed(2)}
                    </div>
                    <div className='col-6 my-3'>
                        Establecimiento
                    </div>
                    <div className='col-6 my-3'>
                        {infoPrecios?.establecimiento?.razonSocial}
                    </div>
                    <div className='col-6 my-3'>
                        Sucursal
                    </div>
                    <div className='col-6 my-3'>
                        {infoPrecios?.sucursal?.direccion}
                    </div>
                </div>
                <div className='row d-flex flex-wrap justify-content-center mt-5'>
                    <button className='btn btn-danger col-3 align-self-center' onClick={() => setInfoPreciosModal(false)}>Cerrar</button>

                </div>
            </Modal>

        </Modal>

    )
}
