import { types } from "../types/types";


const initialState = {
    cheking: true,
    uid: null,
    name: ''

}

export const loginReducer = (state = initialState, action) => {
    switch (action.type) {
        case types.login:
            return {
                ...state,
                uid: action.payload.uid,
                name: action.payload.nombre,
                cheking: false
            }

        case types.checkLogingFinish:
            return {
                ...state,
                cheking: false
            }
        case types.logout:
            return {
                ...state,
                uid: null,
                name: null
            }
        default:
            return state;
    }
}