import Modal from 'react-modal';
import React from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { closeModalFactura } from '../../../actions/facturas';
import moment from 'moment';
import { useState } from 'react';
import { axiosContoken } from '../../../helpers/axiosAPI';
import { ModalProducto } from './ModalProducto';

export const ModalFactura = () => {

    const { factura, modalFactura } = useSelector(state => state.factura)
    const dispatch = useDispatch();
    const [currentProducto, setCurrentProducto] = useState(null)
    const [openModal, setOpenModal] = useState(false)

    const showHistorialProducto = async (producto) => {
        try {
            debugger;
            const response = await axiosContoken(`productos/${producto?.codigoPrincipal}`);
            if (response?.data?.ok) {
                setCurrentProducto({ producto, precios: response?.data?.precios?.sort((a, b) => new Date(a?.fechaRegistro).getTime() - new Date(b?.fechaRegistro).getTime()) })
                setOpenModal(true);
            }
        } catch (error) { }
    }

    return (
        <Modal
            className="modal-add-factura "
            overlayClassName="modal-fondo"

            isOpen={modalFactura}
            //  onAfterOpen={afterOpenModal}
            onRequestClose={() => { }}
            closeTimeoutMS={200}
            shouldCloseOnEsc={true}
            contentLabel="EJEMPLO"

        >
            <div className="container mt-5">
                <div className="row d-flex justify-content-between">
                    <div className='row d-flex flex-wrap border col-8 mx-2'>
                        <div className='col-5'>
                            <b className='d-block'>Fecha emision:</b>
                        </div>
                        <div className='col-6'>
                            {moment(factura?.fechaEmision).format('DD-MM-YYYY')}
                        </div>
                        <div className='col-5'>
                            <b className='d-block'>Total:</b>
                        </div>
                        <div className='col-6'>
                            ${' '}{factura?.importeTotal}
                        </div>
                        <div className='col-5'>
                            <b className='d-block'>Establecimiento:</b>
                        </div>
                        <div className='col-6 text-capitalize'>
                            {factura?.establecimiento?.razonSocial}
                        </div>
                        <div className='col-5'>
                            <b className='d-block'>Sucursal:</b>
                        </div>
                        <div className='col-6'>
                            {factura?.sucursal?.direccion}
                        </div>
                    </div>
                    <div className='col-3'>
                        <button onClick={() => { dispatch(closeModalFactura()) }} className="btn btn-danger form-control">
                            Cerrar
                        </button>
                    </div>
                </div>

                <hr />
                <div className="container border-bottom">
                    <div className='row d-flex flex-wrap border'>
                        <div className='col'>
                            Descripcion
                        </div>
                        <div className='col'>
                            cantidad
                        </div>
                        <div className='col'>
                            Precio unitario
                        </div>
                        <div className='col'>
                            Descuento
                        </div>
                        <div className='col'>
                            Total
                        </div>
                    </div>
                    {
                        factura?.detalles.map(prod => {
                            return <div className='row d-flex flex-wrap border-start border-end p-2 prod-class pointer' onClick={() => showHistorialProducto(prod)} >
                                <div className='col'>
                                    {prod.descripcion}
                                </div>
                                <div className='col'>
                                    {prod.cantidad}
                                </div>
                                <div className='col'>
                                    $ {prod.precioUnitario}
                                </div>
                                <div className='col'>
                                    $ {prod.descuento}
                                </div>
                                <div className='col'>
                                    $ {prod.precioTotalSinImpuesto}
                                </div>
                            </div>
                        })
                    }
                </div>

            </div>
            <ModalProducto openModal={openModal} setOpenModal={setOpenModal} producto={currentProducto} />
        </Modal>
    )
}
