import { types } from "../types/types";

const initialState = {
    modalOpen: false,
    isLoading: false

}

export const uiReducer = (state = initialState, action) => {
    switch (action.type) {
        case types.openModal:

            return {
                ...state,
                modalOpen: true
            };
        case types.closeModal:

            return {
                ...state,
                modalOpen: false
            };

        case types.startLoading:
            return {
                ...state,
                isLoading: true
            }

        case types.finishLoading:
            return {
                ...state,
                isLoading: false
            }

        default:
            return state;
    }
}