import Swal from "sweetalert2";



export const mostrarMensaje = (page = '', texto = '', tipo = 'error',titulo='Error') => {
    switch (page) {
        case 'login':
            Swal.fire({
                title: `<p class="fuente-facturas pop-up-login">${titulo.toUpperCase()}</p>`,
                html: `<p class="fuente-facturas pop-up-login">
                ${texto}
                </p>
                `,
                showConfirmButton: false,
                timer: 2000,
                icon: tipo.toLowerCase(),
                background: '#3D1900AA'
            });
            break;
        case 'upload':
            Swal.fire({
                title: `<p class="fuente-facturas pop-up-login">${titulo.toUpperCase()}</p>`,
                html: `<p class="fuente-facturas pop-up-login">
                ${texto}
                </p>
                `,
                showConfirmButton: false,
                timer: 2000,
                icon: tipo.toLowerCase(),
                background: '#3D1900AA'
            });
            break;

        default:
            Swal.fire({
                title: `<p class="fuente-facturas pop-up-login">${tipo.toUpperCase()}</p>`
            });
            break;
    }
}