import React, { Suspense, useEffect } from 'react'
import {
    BrowserRouter,
    Routes, Route, Navigate
} from "react-router-dom";

import { LoadingScreen } from '../Pages/ui/LoadingScreen';
import { DashBoard } from '../Pages/MainPages/DashBoard';
import { Login } from '../Pages/Login/LoginScreen';
import { useDispatch, useSelector } from 'react-redux';
import { RequireAuth } from './RequireAuth';
import { verifyToken } from '../actions/login';
import { routesMenu } from '../utils/menuOption';

export const AppRouter = () => {

    const { cheking } = useSelector(state => state.login)
    const dispatch = useDispatch();
    useEffect(() => {
        dispatch(verifyToken());
    }, [dispatch]);

    return (cheking) ?
        (<LoadingScreen isLogin={true} />) :
        (<Suspense fallback={<LoadingScreen />}>
            <BrowserRouter>
                <Routes>
                    <Route path="/login" element={<Login />} />
                    <Route
                        path="/"
                        element={
                            <RequireAuth>
                                <DashBoard />
                            </RequireAuth>
                        }
                    >
                        {
                            routesMenu.map(({ path, component: Component }) => (
                                <Route
                                    key={path}
                                    path={path}
                                    element={<Component />} />
                            )
                            )
                        }
                    </Route>
                    <Route path="/*" element={<Navigate to="/" />} />
                </Routes>

            </BrowserRouter>
        </Suspense>
        )
}
