import React, { useState } from 'react'
import fontawesome from '@fortawesome/fontawesome'
import { faEye } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

//import { useDispatch } from 'react-redux';
import { useForm } from '../../../hooks/useForm';
import { mostrarMensaje } from '../../../helpers/popUp';
import { useDispatch, useSelector } from 'react-redux';
import { SmallLoading } from '../../ui/SmallLoading';
import { startRegister } from '../../../actions/login';
fontawesome.library.add(faEye);

export const FormRegisterComponent = () => {
    const dispatch = useDispatch()
    const { isLoading } = useSelector(state => state.ui)

    const [verPassword, setVerPassword] = useState(false);
    const [values, handleInputChange] = useForm({
        correo: '',
        password: '',
        nombre: '',
        password2: ''
    });

    const { correo, password, password2, nombre } = values;

    const submit = (e) => {
        e.preventDefault();
        if (nombre.length <= 3) {
            mostrarMensaje('login', 'El nombre debe ser mayor a 4 caracteres', 'error')
            return;
        }
        if (password.length <= 4) {
            mostrarMensaje('login', 'La contraseña debe ser mayor a 4 caracteres', 'error')
            return;
        }
        if (password !== password2) {
            mostrarMensaje('login', 'Las contraseñas no coinciden', 'error')
            return;
        }
        dispatch(startRegister(correo, password, nombre));
    }
    return (
        <form onSubmit={submit}>
            <h4 className="p-1 fuente-facturas animate__animated animate__bounce animate__fadeInUp animate__fast">
                Registrarse
            </h4>
            <div className="form-group">
                <div className="d-flex align-items-center p-1 animate__animated animate__bounce animate__fadeInUp animate__fast">
                    <input type="text"
                        disabled={isLoading}
                        className="form-control"
                        placeholder="Ingrese un nombre"
                        value={nombre}
                        name="nombre"
                        onChange={handleInputChange} >
                    </input>
                </div>
            </div>
            <div className="form-group">
                <div className=" mt-3 p-1 animate__animated animate__bounce animate__fadeInUp animate__fast">
                    <input
                        disabled={isLoading}
                        type="email"
                        // onInvalid={() => mostrarMensaje('login', 'Correo no válido')}
                        className="form-control "
                        placeholder="Ingrese el correo"
                        value={correo}
                        onChange={handleInputChange}
                        name="correo"
                    />
                </div>
            </div>
            <div className="form-group">
                <div className="d-flex align-items-center p-1 animate__animated animate__bounce animate__fadeInUp animate__fast">
                    <input
                        disabled={isLoading}
                        type={verPassword ? 'text' : 'password'}
                        className="form-control"
                        placeholder="Ingrese el password"
                        value={password}
                        name="password"
                        onChange={handleInputChange} >
                    </input>
                    <div
                        onClick={() => { setVerPassword(!verPassword) }}
                        className={`${(!verPassword) ? 'icon-password' : 'icon-password-active'} p-3`}>
                        <FontAwesomeIcon icon={faEye} size="sm" />
                    </div>
                </div>
            </div>
            <div className="form-group">
                <div className="d-flex align-items-center p-1 animate__animated animate__bounce animate__fadeInUp animate__fast">
                    <input
                        disabled={isLoading}
                        type={verPassword ? 'text' : 'password'}
                        className="form-control"
                        placeholder="Confirme el Password"
                        value={password2}
                        name="password2"
                        onChange={handleInputChange} >
                    </input>
                    <div
                        onClick={() => { setVerPassword(!verPassword) }}
                        className={`${(!verPassword) ? 'icon-password' : 'icon-password-active'} p-3`}>
                        <FontAwesomeIcon icon={faEye} size="sm" />
                    </div>
                </div>
            </div>
            <div className="form-group">
                <div className="animate__animated animate__bounce animate__fadeInUp">
                    <button className="btn btn-primary form-control " disabled={isLoading} type="submit">
                        <div className="d-flex align-items-center justify-content-center">
                            Registrarse  {(isLoading) && <SmallLoading />}
                        </div>

                    </button>
                </div>
            </div>
        </form>
    )
}
